 .c{
    height: 100%;
    position: relative;
    
 }
 .c-bg{
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
 }
.c-wrapper{
    padding: 50px;
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
    text-align: center;
}

.c-title{
    font-size: 60px;
    text-align: center;
}

.c-desc{
    font-weight: 200;
}
form{
    margin-top: 20px;
}
input{
    width: 100%;
    display: block;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button{
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
    cursor: pointer;
    width: 40%;
    font-size: 20px;
}
.c-footer{
   display: flex;
   flex-direction: column;
    text-align: center;
}
.c-footer-title{
    font-size: 24px;
    margin: 1% 5%;
}
.c-footer-wrapper{
 width: 90%;
 margin: 0px auto;
}
.c-footer-item{
    display: inline;
    margin: 0px 10px;
    font-weight: 300;
    font-size: 20px;
   
}
.c-footer small{
    text-align: center;
    margin: 1%;
}
@media screen and (max-width: 678px){
    .c{
        height: 100%;
    }
    
    .c-wrapper{
        flex-direction: column;
    }
     .c-desc{
        display: none;
    }
     .c-title{
        font-size: 52px;
        width: 100%;
    }
    .c-info-item{
        margin: 10px 0px;
        width: 100%;
    }
    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }
    input{
        width: 100%;
        height: 40px;
        margin: 10px;
        margin-left: 0px;
    }
    button{
        margin-top: 20px;
        padding: 15px;
        width: 100%;
        font-size: 20px;
    }
}

@media screen and (max-width: 480px){
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px;
    }
    .c-title{
        font-size: 30px;
    }
    
    .c-desc{
        display: none;
    }
    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }
    input{
        width: 100%;
        height: 40px;
        margin: 10px;
        margin-left: 0px;
    }
    button{
        margin-top: 10px;
    }
    .c-footer{
        text-align: center;
    }
    
    .c-footer-item{
        padding: 1% 0;
        font-size: 15px;
    }
}